h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
}

button {
  cursor: pointer;
  display: block;
  padding: 0;
}

a {
  text-decoration: none;
  margin: 0;
  padding: 0;
  font-family: inherit;
  color: inherit;
  cursor: pointer;
}
